import gsap from 'gsap';


export function bgShow(){
    gsap.from('#tools', {
        duration: 1,
        background: '#fff',
    })
}

export function itemShow(){
    const tl = gsap.timeline();
    gsap.to('.tool_title', {
        duration: 0.6,
        y: 200,
    })
}

export function titleShow(){
    gsap.to('.tool_title', {
        x: -10,
    })
}