                <section class="tool_item col-md-4 col-sm-6" v-for="tag in tagObj.getTag" :key="tag.id">
                    <div class="box1"></div>
                    <div class="box2"></div>
                    <div class="tool_box">
                        <img :src="tag.bg" alt="">
                        <a :href="tag.link" class="target" target="_blank">跳转</a>
                        <h3>{{ tag.name }}</h3>
                    </div>
                </section>
            </div>
        </div>
    </div>
</template>

<script>
    import Title from './components/Title.vue';
    import gsap from 'gsap';

    import {
        getType
    } from '@/api/tag';
    import {
        bgShow,
        itemShow
    } from './anime';

    export default {
        name: "index",
        data(){
            return {
                tagList: [],
            }
        },
        components: {
            Title,
        },
        methods: {

        },
        mounted() {
            bgShow();
            
           
           console.log('Enter Tools');
           getType().then(res=>{
               if(res.length>0){
                   this.tagList = res;
                   this.$nextTick(()=>{
                       gsap.from('.tool_item', {
                           duration: 1,
                           y: 100,
                           stagger: 0.06,
                       })
                   });
               }
           })
        }
    }
</script>

